          nicht registriert sein müssen Sie zuerst einen Account erstellen.
        </router-link>
              <div>
        <div class="custom-btn float-end" :class="user ? '' : 'disabled'" @click="startFitI">Start</div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseUserbar from "@/components/BaseUserbar.vue";

export default {
  name: "InformatikHome",
  components: {
    BaseUserbar,
  },
  props: {},
  emits: [],
  data() {
    return {
      user: {},
      showSortierungTransition: false,
      showUnauthenticatedSorting: false,
      showUnauthenticatedFitI: false,
      headerImg: require("@/assets/inf/informatik_title_image.jpg"),
    };
  },
  mounted() {
    this.user = JSON.parse(window.localStorage.getItem("userData"));    
    this.$store.dispatch('setHeaderPageTitle', 'INFORMATIK OSA');
  },
  methods: {
    startInformatikIntro() {
      this.$router.push("/informatik/einfuehrung");
    },
    startSortierung() {
      if (this.user) {
        this.$router.push("/informatik/sortieren-mit-system");
      } else {
        this.showUnauthenticatedSorting = true;
      }
    },
    startFitI() {
      if (this.user) {
        this.$router.push("/informatik/fit-i");
      } else {
        this.showUnauthenticatedFitI = true;
      }
      //setTimeout(function(){ajaxLogin(document.getElementById('logininp').value,document.getElementById('code').value);},10);
      //https://coping-tests.eu/login.php
    },
  },
};
</script>

